import {useState, useEffect} from 'react';
import { VerifyEmp } from '../../verify/personamData';
import { saveEmployementData, getApplicationById } from '../../Http';
import { toast } from 'react-toastify';
import { useParams } from 'react-router-dom';



const RequirementEmployement = ({currentStep, setCurrentStep, setApplicationData}) => {

    const {id} = useParams()

   const [saving, setSaving] = useState(false)
   const [currentData, setCurrentData] = useState([{
    occupation: null,
    satart_date: null,
    end_date: null,
    company: null,
    type_of_employment: null,
    field_of_experience: null
   }])

   useEffect(() => {
    const fetchData = async () => {
        const {data} = await getApplicationById(id);
        
        if(data.employement.length > 0){
            setCurrentData(data.employement)
        }
        console.log('emp', data)
        
    }
    fetchData()
}, [])

const handleAddMore = () => {
    setCurrentData([
        ...currentData,{
            occupation: null,
    satart_date: null,
    end_date: null,
    company: null,
    type_of_employment: null,
    field_of_experience: null
        }
    ])
}

   const handleChnage = (e, i) => {
    const upd = [...currentData]
    upd[i][e.target.name] = e.target.value
    setCurrentData(upd)
    console.log(currentData)
   }

   const handleNext = async () => {
   
    currentData.forEach((cd) => {
        if(!VerifyEmp(cd)){
            return (
                toast.error('Every field is required', {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }
    })

    setSaving(true)

    currentData.forEach(async (cd) => {

        const {data} = await saveEmployementData({
            ...cd,
            application_id: id
        })

    })
    const application = await getApplicationById(id);
    setApplicationData(application.data)
   
    
    ///setCurrentStep(7)
    setSaving(false)
   }

    return (
        <div className="w-full">
            <h1 className="text-xl font-semibold mr-6 my-6">Employement Detiles</h1>
            {
                currentData.map((current, i) => (
                    <div className="xl:w-[60%] mt-[50px]">
                    <div className="flex flex-col my-2 md:flex-row md: justify-between">
                        <div className="w-full md:w-[49%]">
                           <label className="text-xs font-bold">Occupation  <span className='text-xs text-red-600 ml-1'>*</span></label>
                           <input onChange={(e) => handleChnage(e, i)} className='wizard_input' name='occupation' value={current.occupation} placeholder="Your occupation" />
                        </div>
                        <div className="w-full md:w-[49%]">
                           <label className="text-xs font-bold">Company  <span className='text-xs text-red-600 ml-1'>*</span></label>
                           <input onChange={(e) => handleChnage(e, i)} className='wizard_input'  name='company' value={current.company} placeholder="Company name" />
                        </div>
                     </div>
                     <div className="flex my-2 flex-col md:flex-row md: justify-between">
                       <div className="w-full md:w-[49%]">
                           <label className="text-xs font-bold">Start Year  <span className='text-xs text-red-600 ml-1'>*</span></label>
                           <input onChange={(e) => handleChnage(e, i)} type="text" className='wizard_input' placeholder="select a Test" value={current.satart_date} name='satart_date' />
                        </div>
                        <div className="w-full md:w-[49%]">
                           <label className="text-xs font-bold">End Year</label>
                           <input onChange={(e) => handleChnage(e, i)} type="text" className='wizard_input' value={current.end_date} placeholder="select a Test" name='end_date' />
                        </div>
                       
                     </div>
                     
                     <div className="flex my-2 flex-col md:flex-row md: justify-between">
                     
                        <div className="w-full">
                        <label className="text-xs font-bold">Type of employement  <span className='text-xs text-red-600 ml-1'>*</span></label>
                        <input onChange={(e) => handleChnage(e, i)} type="text" className='wizard_input' value={current.type_of_employment} placeholder="select a Test" name='type_of_employment' />
                        {/* <div className="grid grid-cols-2 gap-4 md:grid-cols-4 mb-4 mt-2">
                         <div className="flex items-center mr-6">
                             <label htmlFor={`gov-${i}`}>
                                 <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                                 <input onChange={(e) => handleChnage(e, i)} checked={current.type_of_employment === 'Goverment' ? true : false} type='radio' name="type_of_employment" className="check_input hidden" id={`gov-${i}`} value='Goverment' />
                                     <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                                 </div>
                             </label>
                             <div><span className="ml-2">Goverment</span></div>
                         </div>
                         <div className="flex items-center mr-6">
                             <label htmlFor={`pri-${i}`}>
                                 <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                                 <input onChange={(e) => handleChnage(e, i)} type='radio' checked={current.type_of_employment === 'Private' ? true : false} name="type_of_employment" className="check_input hidden" id={`pri-${i}`} value='Private' />
                                     <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                                 </div>
                             </label>
                             <div><span className="ml-2">Private</span></div>
                         </div>
                        </div> */}
                        </div>
            
                        
                     </div>
            
                     <div className="flex my-2 flex-col md:flex-row md: justify-between">
                     
                        <div className="w-full">
                        <label className="text-xs font-bold">Field of exprience  <span className='text-xs text-red-600 ml-1'>*</span></label>
                        <input onChange={(e) => handleChnage(e, i)} type="text" className='wizard_input' value={current.field_of_experience} placeholder="select a Test" name='field_of_experience' />
                        </div>
            
                        
                     </div>
                      
                    
            
                  
                    </div>
                ))
            }
            <div className='w-full xl:w-[60%]'>
              <button onClick={handleAddMore} className='w-full text-sm border-2 border-black opacity-50 rounded-3xl h-[40px] my-6 flex justify-center items-center'>+ Add More</button>
               <div className="mt-4 flex items-center justify-between">
                     <button
                        onClick={() => setCurrentStep(5)}
                        className="text-sm font-bold text-[#ffffff] bg-black py-2 px-6 rounded-3xl inline-flex items-center">
                            Back</button>
                    <button
                     onClick={handleNext}
                     className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center">
                         {saving ? <span className='load'></span> : ''}
                         Next</button>
                 </div>
                 </div>
     </div>
    )
}

export default RequirementEmployement;