import { useState, useEffect } from "react";
import { getApplication } from "../../Http";
import Loader from "../../components/Loader/Loader";
import { useNavigate } from "react-router-dom";


const AplicationAll = () => {

    const [applications, setApplications] = useState([]);
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();

    useEffect(() => {
        const fetchApplications = async () => {
          const { data } = await getApplication();
          setApplications(data)
          console.log(data)
        }
        fetchApplications();
      }, [])

    return (
       <>
       <div
                    className="top-filter flex justify-between mt-8">
                    <div
                      className="left flex items-center">
                      <div className="flex items-end">
                        <h1 className="text-2xl font-semibold mr-6 ">All Aplication</h1>
                        {/* <span className="text-xs text-[#AEAEAE]">98</span> */}
                      </div>
      
      
                    </div>
                    <div className="right flex">
                      <button className="mx-2 text-sm text-[#AEAEAE] font-semibold border border-[#AEAEAE] px-6 py-1 rounded-3xl"><i class="fa-solid fa-filter mr-3"></i> <span>Filter</span></button>
      
                      <div className="border relative border-[#AEAEAE] px-6 py-1 rounded-3xl text-sm text-[#AEAEAE]">
                        <i class="fa-solid fa-arrow-down-short-wide mr-4"></i>
                        <span className="mr-4">Sort</span>
                        <span className="transition-transform 
                                       duration-500
                                       peer-checked:rotate-90"><i class="fa-solid fa-chevron-down"></i></span>
      
                        <input
                          type="checkbox" checked
                          className="
                                       peer
                                       absolute top-0 inset-x-0 
                                       w-full h-12
                                       opacity-0 z-10 cursor-pointer
                                       "
                        />
                      </div>
                    </div>
                  </div>
      
      
                  <div className="w-full">
      
                  {loading && <Loader />}
      
{
  applications.length > 0 ? (
    <table className="w-full">
    <thead className="bg-[#FBFBFB] px-2 rounded-sm mb-1">
        <td className="py-2 px-2">
            <input type='radio' />
        </td>
        <td className="text-[#7C878E] text-nl">
        Application No.
        </td>
        <td className="text-[#7C878E] text-nl">Institution</td>
        <td className="text-[#7C878E] text-nl">Course</td>
        <td className="text-[#7C878E] text-nl">Course Start Date</td>
        <td className="text-[#7C878E] text-nl">Application Fees</td>
        <td className="text-[#7C878E] text-nl">Status</td>
        <td className="text-[#7C878E] text-nl"></td>
    </thead>
    <tbody className="mt-4">
        


      {
        applications.map((application, i) => (
          <tr className="bg-[#ffffff] my-4" key={i}>
          <td className="px-2 py-6">
              <input type="radio" />
          </td>
          <td>
              <p className="text-nl">{application._id.substring(1, 6)}..</p>
          </td>
          <td>
              <p className="text-nl  w-[200px] whitespace-nowrap my-elips overflow-hidden text-ellipsis hover:overflow-visible hover:whitespace-normal duration-200">{application.course.aa_institute.university_name}</p>
              {/* <p className="text-nl">ID: {user.personal_detail.user_id.substring(0, 6)}...</p> */}
          </td>
          <td className="">
              <p className="text-nl w-[200px] whitespace-nowrap my-elips overflow-hidden text-ellipsis hover:overflow-visible hover:whitespace-normal">{application.course.course_name}</p>
          </td>
          <td>
              <p className="text-nl">{application.createdAt ? application.createdAt.substring(0, 10) : 'N/A'}</p>
          </td>
          <td>
              <p className="text-nl">$: {application.course.application_fees}</p>
          </td>
          <td>
              <p className="text-nl">{application.status}</p>
          </td>
          <td>
            <button
             onClick={() => navigate(`/applications/view/${application._id}`)}
             className="text-sm font-semibold rounded-3xl text-[#ffffff] mr-4 bg-primary py-1 px-4 border-[2px] border-primary">View</button>
          </td>
      </tr>
        ) )
      }
          
       
    </tbody>
</table>
  ) : (
    <div className="w-full h-[300px] flex justify-center items-center">
      <p className="text-xl opacity-50">No applications found</p>
    </div>
  )
}
      
                   
                 
      
                  </div>
      
       </>
    )
}

export default AplicationAll;