import SetupCard from "../../components/WixardCard/SetupCard";
import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setStanderTest, setAuth } from "../../store/authSlice";
import { saveStaTest, getStaTest, getApplicationById } from "../../Http";
import { toast } from 'react-toastify';
import { useParams } from "react-router-dom";


const RequirementStander = ({standerData, currentStep, setCurrentStep, setApplicationData}) => {

 
    const [stepData, setStepData] = useState({
        test_name: null,
        certificate: null
    })
    const {id} = useParams()
    const [saving, setSaving] = useState(false);
    const [file, setFile] = useState('Chhose a file')


    useEffect(() => {
        const fetchData = async () => {
            const {data} = await getApplicationById(id);
            if(data.standerd_test){
                setStepData(data.standerd_test)
            }
            
            console.log('Stan', data)
            
        }
        fetchData()
    }, [])


    const handleChange = (e) => {
        setStepData({
            ...stepData,
            [e.target.name]: e.target.value
        })
    }

    const handleNext = async () => {

        if(!stepData.test_name){
            return (
                toast.error('Test name is required', {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }
        setSaving(true);
        const {data} = await saveStaTest({
            ...stepData,
            application_id: id
        });
        const application = await getApplicationById(id);
        setApplicationData(application.data)
        setCurrentStep(6)
        console.log(data)
    }

    const handleFile = (e) => {
        const uploaderFile = e.target.files[0];
        setFile(uploaderFile.name);
        const reader = new FileReader();
        reader.readAsDataURL(uploaderFile);
        reader.onload = function(){
            setStepData({
                ...stepData,
                certificate: reader.result
            })
            console.log(stepData)
        }
    }


    

    useEffect(() => console.log(stepData), [stepData])
    return (
        <div className="xl:w-[70%]">
            <h1 className="text-xl font-semibold mr-6 my-6">Standered Test Detiles</h1>
             <div className="flex w-full justify-between mt-4">
           <div className="w-full">
               <label className="text-xs font-bold">Standardised Tests <span className='text-xs text-red-600 ml-1'>*</span></label>
               <div className="flex mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="gre">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={stepData.test_name === 'GRE' ? true : false} type='radio' name="test_name" value='GRE' onChange={(e) => handleChange(e)} className="check_input hidden" id="gre" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">GRE</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="gmat">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={stepData.test_name === 'GMAT' ? true : false} type='radio' name="test_name" value='GMAT' className="check_input hidden" id="gmat" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">GMAT</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="sat">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={stepData.test_name === 'SAT' ? true : false} type='radio' name="test_name" value='SAT' className="check_input hidden" id="sat" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">SAT</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="not">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={stepData.test_name === 'Not Applicable' ? true : false} type='radio' name="test_name" value='Not Applicable' className="check_input hidden" id="not" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Not Applicable</span></div>
                </div>
               </div>
           </div>
           
        </div>
        <div className="flex w-full justify-between mt-4">
           <div className="w-full">
               <label htmlFor="cert" className="text-xs font-bold">Mark Sheet/Certificate
               <div className="border-2 border-primary h-[45px] flex justify-center items-center pl-2 rounded-3xl text-primary text-center">
                    {file}
                </div>
               </label>
               <input type='file' id='cert' className="hidden" name="certificate" onChange={(e) => handleFile(e)} />
           </div>
           
           
        </div>



        <div className="mt-4 flex items-center justify-between">
        <button
           onClick={() => setCurrentStep(4)}
            className="text-sm font-bold text-[#ffffff] bg-black py-2 px-6 rounded-3xl inline-flex items-center">
                Back</button>
           <button
           onClick={handleNext}
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center">
                {saving ? <span className='load'></span> : ''}
                Next</button>
        </div>
       
        </div>
    )
}

export default RequirementStander;