import SetupCard from "../../components/WixardCard/SetupCard";
import { getAreaOfStudy, getCourseLevels, getFieldOfStudy, getApplicationById } from "../../Http";
import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { toast } from 'react-toastify';
import { saveStudyPref } from "../../Http";
import { setStudyPref, setAuth } from "../../store/authSlice";
import { useParams } from "react-router-dom";


const RequirementPreference = ({preferaenceData, currentStep, setCurrentStep, setApplicationData}) => {

    const [areaOfStudy, setAreaOfStudy] = useState([])
    const [lavelOfStudy, setLavelOfStudy] = useState([]);
    const {id} = useParams()

    const [currentData, setCurrentData] = useState({
        education_country: [null],
        field_of_study: null,
        level_of_study: null,
        start_year: null
    })

    
    useEffect(() => {
        const fetchData = async () => {
            const {data} = await getApplicationById(id);
            if(data.study_preference){
                setCurrentData(data.study_preference)
            }
            
            console.log('pref', data)
            
        }
        fetchData()
    }, [])

    const [saving, setSaving] = useState(false);

  




    const handleChange = (e) => {
        setCurrentData({
            ...currentData,
            [e.target.name]: e.target.value
        })
        console.log(currentData)
    }

    const handleCheckbox = (e) => {
       if(e.target.checked){
        let arr = [...currentData.education_country, e.target.value];
        setCurrentData({
            ...currentData,
            education_country: arr
        })
       }else if(!e.target.checked){
        let arr = [...currentData.education_country];
        let index = arr.findIndex((a) => a === e.target.value);
        if(index != -1){
            arr.splice(index, 1)
        }
        setCurrentData({
            ...currentData,
            education_country: arr
        })
        console.log(currentData)
       }

        
    }


    useEffect(() => {
        const area = async () => {
            const {data} = await getFieldOfStudy();
            setAreaOfStudy(data)
        }
        area()
    }, [])

    useEffect(() => {
        const lavel = async () => {
            const {data} = await getCourseLevels();
            setLavelOfStudy(data)
        }
        lavel()
    }, [])



    const handleNExt = async () => {
        if(!currentData.education_country || !currentData.field_of_study || !currentData.level_of_study || !currentData.start_year){
            return (
                toast.error('Every fields are required', {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }
        setSaving(true)

        const { data } = await saveStudyPref({
            ...currentData,
            application_id: id
        });
        const application = await getApplicationById(id);
        setApplicationData(application.data)
        setCurrentStep(5)
        console.log(data)
    }

    

    return (
        <div className="xl:w-[70%]">
            <h1 className="text-xl font-semibold mr-6 my-6">Study Preference Detiles</h1>
            <div className="flex w-full justify-between mt-8">
           <div className="w-full">
               <label className="text-xs font-bold">Your dream country <span className='text-xs text-red-600 ml-1'>*</span></label>
               <div className="grid grid-cols-2 gap-4 md:grid-cols-4 mb-4 mt-2 xl:grid-cols-5">
                <div className="flex items-center mr-6">
                    <label htmlFor="aus">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={currentData.education_country.includes('Australia') ? true : false}  type='checkbox' className="check_input hidden" id="aus" value='Australia' onChange={(e) => handleCheckbox(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Australia</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="can">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={currentData.education_country.includes('Canada') ? true : false} type='checkbox' className="check_input hidden" id="can" value='Canada' onChange={(e) => handleCheckbox(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Canada</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="us">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={currentData.education_country.includes('Usa') ? true : false} type='checkbox' className="check_input hidden" id="us" value='Usa' onChange={(e) => handleCheckbox(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Usa</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="uk">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={currentData.education_country.includes('UK') ? true : false} type='checkbox' className="check_input hidden" id="uk" value="UK" onChange={(e) => handleCheckbox(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">UK</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="oth">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={currentData.education_country.includes('Others') ? true : false} type='checkbox' className="check_input hidden" id="oth" value="Others" onChange={(e) => handleCheckbox(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Others</span></div>
                </div>
               </div>
           </div>
           
        </div>
        <div className="flex flex-wrap w-full justify-between mt-6">
           <div className="w-full md:w-[49%]">
               <label className="text-xs font-bold">Prefered field of study <span className='text-xs text-red-600 ml-1'>*</span></label>
               <select className='wizard_input' name='field_of_study' placeholder="select Gender" onChange={(e) => handleChange(e)}>
                        {
                            areaOfStudy.map((a) => (
                                <option selected={currentData.field_of_study === a.title ? true : false} key={a._id} value={a.title}>{a.title}</option>
                            ))
                        }
                </select>
           </div>
           <div className="w-full md:w-[49%]">
               <label className="text-xs font-bold">Prefered lavel of study <span className='text-xs text-red-600 ml-1'>*</span></label>
               <select className='wizard_input' name='level_of_study' placeholder="select Gender" onChange={(e) => handleChange(e)}>
               {
                            lavelOfStudy.map((l) => (
                                <option selected={currentData.level_of_study === l.title ? true : false} key={l._id} value={l.title}>{l.title}</option>
                            ))
                        }
                </select>
           </div>
           
        </div>

        <div className="flex w-full justify-between mt-6">
           <div className="w-full">
               <label className="text-xs font-bold">When do you want to start study <span className='text-xs text-red-600 ml-1'>*</span></label>
               <div className="grid grid-cols-2 md:grid-cols-3 gap-4 mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="st">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={currentData.start_year === '1st half 2023' ? true : false} type='radio' name="start_year" value='1st half 2023' onChange={(e) => handleChange(e)} className="check_input hidden" id="st" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">1<sup>st</sup> Half 2023</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="nd">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={currentData.start_year === '2nd half 2030' ? true : false} type='radio' name="start_year" value='2nd half 2030' className="check_input hidden" id="nd" onChange={(e) => handleChange(e)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">2<sup>nd</sup> Half 2023</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="ny">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={currentData.start_year === 'Next Year' ? true : false} type='radio' name="start_year" value='Next Year' onChange={(e) => handleChange(e)} className="check_input hidden" id="ny" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Next Year</span></div>
                </div>
               </div>
               
           </div>
          
           
        </div>
       
       
       
       

        <div className="mt-4 flex items-center justify-between">
        <button
           onClick={() => setCurrentStep(3)}
            className="text-sm font-bold text-[#ffffff] bg-black py-2 px-6 rounded-3xl inline-flex items-center">
                Back</button>
           <button
           onClick={handleNExt}
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center">
                {saving ? <span className='load'></span> : ''}
                Next</button>
        </div>
        </div>
    )
}

export default RequirementPreference;