import { useNavigate, useParams } from "react-router-dom";
import { Timeline } from "antd";
import { useState, useEffect } from "react";
import { getApplicationById } from "../../Http";
import Loader from '../../components/Loader/Loader';
import RequirementPersonal from './RequirementPersonal';
import RequirementPreference from './RequirementPreference';
import RequirementEducation from './RequirementEducation';
import RequirementEnglish from './RequirementEnglish';
import RequirementStander from './RequirementStander';
import RequirementEmployement from "./RequirementEmployement";
import RequirementDocument from "./RequirementDocument";
import { VerifyPersonalData } from "../../verify/personamData";
import { VerifyEducationData } from "../../verify/personamData";
import { VerifyEnglishData } from "../../verify/personamData";
import { VerifyEmp } from "../../verify/personamData";
import { VerifyPref } from "../../verify/personamData";
import { VerifyStander } from "../../verify/personamData";
import { submieApplication } from "../../Http";

const ApplicationView = () => {

    const navigate = useNavigate()
    const { id } = useParams()
    const [item, setItem] = useState([{label:'Pending'},{label:'Submited'},{label:'Review'}])
    const [applicationData, setApplicationData] = useState({});
    const [loading, setLoading] = useState(true)
    const [currentStep, setCurrentStep] = useState(1);
    const [submitting, setSubmiting] = useState(false)
    const [allowSubmit, setAllowSubmit] = useState(false)




    useEffect(() => {
        const fetchData = async () => {
            const {data} = await getApplicationById(id);
            setApplicationData(data)
            console.log(data)
            setLoading(false)
        }
        fetchData()
    }, [])

    useEffect(() => {
        if(applicationData.personal_info && applicationData.edu_info && applicationData.employement && applicationData.english_test && applicationData.study_preference && applicationData.standerd_test){
            if(VerifyPersonalData(applicationData.personal_info) && VerifyEducationData(applicationData.edu_info) && VerifyEmp(applicationData.employement[0]) && VerifyPref(applicationData.study_preference) && VerifyStander(applicationData.standerd_test)){
                setAllowSubmit(true)
                return
            }
            if(!VerifyEducationData(applicationData.edu_info[0])){
             return setAllowSubmit(false)
            }
            if(!VerifyEnglishData(applicationData.english_test[0])){
             return setAllowSubmit(false)
            }
            if(!VerifyPref(applicationData.study_preference)){
             return setAllowSubmit(false)
            }
            if(!VerifyStander(applicationData.standerd_test)){
             return setAllowSubmit(false)
            }
            setAllowSubmit(true)
        }
       
    }, [applicationData])

    const handleSubmit = async () => {
        setSubmiting(true)
        const {data} = await submieApplication({id});
        const newApp = await getApplicationById(id);
        setApplicationData(newApp.data)
        setSubmiting(false)
    }

    if(loading){
        return <Loader />
    }


    return (
        <div>
            <div>
              <h1 className="text-2xl font-semibold mr-6 ">Application Status</h1>
            </div>
            <div className="flex flex-col md:flex-row justify-between mt-4">
                <div className="inner relative border-[#AEAEAE] rounded-lg border-[2px] p-4 w-full md:w-[78%]">
                  <p className="text-sm"><span>{applicationData.course.course_level}</span></p>
                  <div className="card-title h-[50px] my-6 flex justify-between">
                     <div>
                     <span className="text-xs opacity-[.5]">Course Code : {applicationData.course.cricos_code}</span>
                      <h1 className="md:text-xl text-lg font-semibold">{applicationData.course.course_name}</h1>
                     </div>
                      <button className="text-sm font-semibold rounded-3xl mr-4 hover:bg-[#FFC9CF] text-primary py-2 md:px-6" onClick={() => navigate(`/courses/course/}`)}>Program Details</button>
                  </div>
                  <div className="university flex w-full mb-4 justify-between">
                    <p className="md:text-md text-sm font-semibold">{applicationData.course.aa_institute.university_name}</p>
                    <span className="flex text-sm md:text-md mx-6 font-semibold"><img className="mx-2" src='/assets/icons/place.svg' />{applicationData.course.aa_institute.university_address}</span>
                </div>
                <div>
                    <p>Status : {applicationData.status}</p>
                </div>
                </div>
                <div className="w-full md:w-[20%] lg:bg-[#E2E2E2] rounded-xl flex justify-center items-center">
                    <button className="border-2 border-primary text-primary bg-white py-2 px-6 rounded-3xl">View Application</button>
                </div>
            </div>
            <div className="w-full">
               <div className="w-full">
                {/* <ul className="flex items-center w-full">
                  {
                    item.map((it,i) => (
                        <li className={`flex flex-col justify-center w-[${100 / (item.length - 1)}%]`}>
                                                  {
                            (i + 1) % 2 == 0 ? (
                                <div className="">
                                    <p>{it.label}</p>
                                </div>
                            ) : ''
                         }
                         <div className="flex items-center">
                           <div className="w-[5px] h-[5px] bg-primary rounded-[50%]"></div>
                           {(i + 1) === item.length ? '' : <div className={`h-[2px] w-full min-w-[130px] bg-gray-600`}></div>}
                         </div>
                         {
                            (i + 1) % 2 != 0 ? (
                                <div>
                                    <p>{it.label}</p>
                                </div>
                            ) : ''
                         }
                        </li>
                    ))
                  }
                </ul> */}
               </div>
            </div>
            <div className="mt-6">
               <div>
                  <h1 className="text-2xl font-semibold mr-6 ">Application Requirements</h1>
               </div>
              {applicationData.status === 'Pending' ? (<><div>
                  { currentStep === 1 ? <RequirementPersonal setApplicationData={setApplicationData} currentStep={currentStep} setCurrentStep={setCurrentStep} personalData={applicationData.personal_info} /> : null}
                  { currentStep === 2 ? <RequirementEducation setApplicationData={setApplicationData} currentStep={currentStep} setCurrentStep={setCurrentStep} educationData={applicationData.edu_info} /> : null}
                  { currentStep === 3 ? <RequirementEnglish setApplicationData={setApplicationData} currentStep={currentStep} setCurrentStep={setCurrentStep} englishData={applicationData.english_test} /> : null }
                  { currentStep === 4 ? <RequirementPreference setApplicationData={setApplicationData} currentStep={currentStep} setCurrentStep={setCurrentStep} preferaenceData={applicationData.study_preference} /> : null }
                  { currentStep === 5 ? <RequirementStander setApplicationData={setApplicationData} currentStep={currentStep} setCurrentStep={setCurrentStep} standerData={applicationData.stander_test} /> : null }
                  {currentStep === 6 ? <RequirementEmployement setApplicationData={setApplicationData} currentStep={currentStep} setCurrentStep={setCurrentStep} /> : null }
               </div>
               <div className="mt-4 w-full xl:w-[60%]">
               {allowSubmit ? <button
               onClick={handleSubmit}
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center w-full w-full justify-center">
                {submitting ? <span className='load'></span> : ''}
                Submit</button> : 
                <button
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center w-full justify-center opacity-40" disabled={true}>
                Submit</button>}
               </div></>) : null }
            </div>
        </div>
    )
}

export default ApplicationView;