import { useState, useEffect } from "react";
import { VerifyEducationData } from "../../verify/personamData";
import { toast } from 'react-toastify';
import { saveEducationData, getApplicationById } from "../../Http";
import { useParams } from "react-router-dom";


const RequirementEducation = ({educationData, currentStep, setCurrentStep, setApplicationData}) => {

    const [saving, setSaving] = useState(false);
    const { id } = useParams()
    const [currentDta, setCurrentData] = useState([{
        level_of_education: null,
        degree_name: null,
        name_of_institution: null,
        attended_from: null,
        course_status: null,
        attended_till: null,
        primary_language_of_instruction: null,
        score_type: null,
        marks: null,
        certificate: null,
        country_of_institution: null
    }])
    const [file, setFile] = useState('Select')


    useEffect(() => {
        const fetchData = async () => {
            const {data} = await getApplicationById(id);
            if(data.edu_info[0]){
                setCurrentData(data.edu_info)
            }
            
            console.log('Education', data)
            
        }
        fetchData()
    }, [])

    const handleChange = (e, i) => {
        const updOpt = [...currentDta];
        updOpt[i][e.target.name] = e.target.value;
        if(e.target.value === 'Studying' && e.target.name === 'course_status'){
            updOpt[i].marks = null;
            updOpt[i].certificate = null;
            updOpt[i].attended_till = null
        }
        setCurrentData(updOpt);
        console.log(currentDta)
        
    }

    const handleCertificate = (e, i) => {

        const uploaderFile = e.target.files[0];
        setFile(uploaderFile.name);
        const reader = new FileReader();
        reader.readAsDataURL(uploaderFile);
        reader.onload = function(){
            let opt = [...currentDta]
            opt[i].certificate = reader.result
            setCurrentData(opt)
        }

        console.log(currentDta)
        
    }

    const handleUpdate = async (i) => {
        if(!VerifyEducationData(currentDta[i])){
            return (
                toast.error('Every field is required', {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }

        setSaving(true);

        const {data} = await saveEducationData({
            ...currentDta[i],
            application_id: id
        });
        const application = await getApplicationById(id);
        setApplicationData(application.data)
        setCurrentStep(3)
        console.log(data)

        
    }

    return (
        <div>
             <h1 className="text-xl font-semibold mr-6 my-6">Educational Detiles</h1>
            <div className="w-full">
               {
                currentDta.map((cdata, i) => (
                    <div className="xl:w-[70%]">
           <div className="flex flex-col my-2 md:flex-row md: justify-between">
               <div className="w-full">
                  <label className="text-xs font-bold">Lavel of education <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <div className="grid grid-cols-2 gap-4 md:grid-cols-4 mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="10th">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="level_of_education" checked={currentDta[i].level_of_education === '10th' ? true : false} onChange={(e) => handleChange(e, i)} className="check_input hidden" id="10th" value='10th' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">10th</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="12th">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="level_of_education" onChange={(e) => handleChange(e, i)} checked={currentDta[i].level_of_education === '12th' ? true : false} className="check_input hidden" id="12th" value='12th' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">12th</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="bach">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="level_of_education" onChange={(e) => handleChange(e, i)} checked={currentDta[i].level_of_education === 'Bachelors Degree' ? true : false} className="check_input hidden" id="bach" value='Bachelors Degree' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Bachelor Degree</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="mas">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="level_of_education" onChange={(e) => handleChange(e, i)} checked={currentDta[i].level_of_education === 'Master Degree' ? true : false} className="check_input hidden" id="mas" value='Master Degree' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Masters Degree</span></div>
                </div>
               </div>
               </div>
            </div>
            <div className="flex my-2 flex-col md:flex-row md: justify-between">
              <div className="w-full md:w-[49%]">
                  <label className="text-xs font-bold">Degree Name <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input type="text" name='degree_name' value={currentDta[i].degree_name} onChange={(e) => handleChange(e, i)} className='wizard_input' placeholder="select a Test" />
               </div>
               <div className="w-full md:w-[49%]">
                  <label className="text-xs font-bold">Name of Institution <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input type="text" value={currentDta[i].name_of_institution} onChange={(e) => handleChange(e, i)} className='wizard_input' placeholder="select a Test" name='name_of_institution' />
               </div>
            </div>

            <div className="flex flex-col my-2 md:flex-row md: justify-between">
               <div className="w-full">
                  <label className="text-xs font-bold">Language of education <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <div className="grid grid-cols-2 gap-4 md:grid-cols-4 mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="hin">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' onChange={(e) => handleChange(e, i)} checked={currentDta[i].primary_language_of_instruction === 'Hindi' ? true : false} name="primary_language_of_instruction" className="check_input hidden" id="hin" value='Hindi' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Hindi</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="eng">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="primary_language_of_instruction" onChange={(e) => handleChange(e, i)} checked={currentDta[i].primary_language_of_instruction === 'English' ? true : false} className="check_input hidden" id="eng" value='English' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">English</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="loth">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="primary_language_of_instruction" onChange={(e) => handleChange(e, i)} checked={currentDta[i].primary_language_of_instruction === 'Others' ? true : false} className="check_input hidden" id="loth" value='Others' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Others</span></div>
                </div>


               </div>
               </div>
            </div>

            <div className="flex my-2 flex-col md:flex-row md: justify-between">
              <div className="w-full md:w-[49%]">
                  <label className="text-xs font-bold">Starting Year <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input type="text" name='attended_from' onChange={(e) => handleChange(e, i)} value={currentDta[i].attended_from} className='wizard_input' placeholder="select a Test" />
               </div>
               <div className="w-full md:w-[49%]">
                  <label className="text-xs font-bold">Country of education <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input type="text" value={currentDta[i].country_of_institution} onChange={(e) => handleChange(e, i)} className='wizard_input' placeholder="select a Test" name='country_of_institution' />
               </div>
               
            </div>

            


            <div className="flex flex-col my-2 md:flex-row md: justify-between">
               <div className="w-full">
                  <label className="text-xs font-bold">Course Status <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <div className="grid grid-cols-2 gap-4 md:grid-cols-4 mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="stud">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="course_status" onChange={(e) => handleChange(e, i)} checked={currentDta[i].course_status === 'Studying' ? true : false} className="check_input hidden" id="stud" value='Studying' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Studying</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="comp">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="course_status" onChange={(e) => handleChange(e, i)} checked={currentDta[i].course_status === 'Completed' ? true : false} className="check_input hidden" id="comp" value='Completed' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Completed</span></div>
                </div>
               </div>
               </div>
            </div>

            
            {
                currentDta[i].course_status === "Completed" ? (
                    <div className="flex flex-wrap my-2 flex-col md:flex-row md: justify-between">
              <div className="w-full md:w-[49%]">
                  <label className="text-xs font-bold">Marks(%) <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input type="number" value={currentDta[i].marks} onChange={(e) => handleChange(e, i)} className='wizard_input' placeholder="select a Test" name='marks' />
               </div>
               <div className="w-full md:w-[49%]">
                  <label htmlFor="cert" className="text-xs font-bold">Certificate
                    <div className="border-2 border-primary h-[45px] flex items-center pl-2 rounded-3xl text-primary">
                       {file}
                    </div>
                  </label>
                  <input type='file' onChange={(e) => handleCertificate(e, i)} id='cert' className='hidden' name='certificate' />
               </div>

               <div className="w-full">
                  <label className="text-xs font-bold">Completed Year <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input type="number" name='attended_till' value={currentDta[i].attended_till} onChange={(e) => handleChange(e, i)} className='wizard_input' placeholder="select a Test" />
               </div>

               
               
            </div>
                ) : ''
            }
 
            
             
           

            
            <div className="mt-4 flex justify-between">
            <button
           onClick={() => setCurrentStep(1)}
            className="text-sm font-bold text-[#ffffff] bg-black py-2 px-6 rounded-3xl inline-flex items-center">
                Back</button>
           <button
           onClick={() => handleUpdate(i)}
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center">
                {saving ? <span className='load'></span> : ''}
                Next</button>
        </div>
           </div>
                ))
               }
            </div>
        </div>
    )
}

export default RequirementEducation;