
import { useState, useEffect } from "react";
import { toast } from 'react-toastify';
import { saveEnglishData, getApplicationById } from "../../Http";
import { VerifyEnglishData } from "../../verify/personamData";
import { useParams } from "react-router-dom";


const RequirementEnglish = ({englishData, currentStep, setCurrentStep, setApplicationData}) => {
   

    const { id } = useParams()
    const [saving, setSaving] = useState(false);
    const [currentData, setCurrentData] = useState([{
        exam_status: null,
        test_name: null,
        test_date: null,
        speaking: null,
        listening: null,
        writing: null,
        reading: null,
        over_all: null,
        need_help: null
    }])

    useEffect(() => {
        const fetchData = async () => {
            const {data} = await getApplicationById(id);
            
            if(data.english_test[0]){
                setCurrentData(data.english_test)
            }
            console.log('nglist', data)
            
        }
        fetchData()
    }, [])

    const handleChange = (e, i) => {

        const updOpt = [...currentData];
        updOpt[i][e.target.name] = e.target.value
        if(e.target.name === 'exam_status' && e.target.value === "Yet to give exam"){
            updOpt[i].need_help = true
            updOpt[i].test_name = null;
            updOpt[i].speaking = null;
            updOpt[i].reading = null;
            updOpt[i].over_all = null;
            updOpt[i].listening = null;
            updOpt[i].test_date = null
        }
        setCurrentData(updOpt)
        console.log(currentData)
    }

    const handleCheckBOx = (e, i) => {
        const updOpt = [...currentData];
        if(e.target.checked){
            updOpt[i].need_help = true
            updOpt[i].test_name = null;
            updOpt[i].speaking = null;
            updOpt[i].reading = null;
            updOpt[i].over_all = null;
            updOpt[i].listening = null;
            updOpt[i].test_date = null
        }else{
            updOpt[i].need_help = false
        }
        
        setCurrentData(updOpt)
        console.log(currentData)
    }

    const handleUpdate = async (i) => {

        if(!VerifyEnglishData(currentData[i])){
            return (
                toast.error('Every field is required', {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }

        setSaving(true)
        const {data} = await saveEnglishData({
            ...currentData[i],
            application_id: id
        })
        const application = await getApplicationById(id);
        setApplicationData(application.data)
        setCurrentStep(4)
        console.log(data)

    }

   

  


   
    return (
        <div className="w-full">
            <h1 className="text-xl font-semibold mr-6 my-6">English Test Detiles</h1>
            {
                currentData.map((eng, i) => (
                    <div className="xl:w-[60%]">
            <div className="flex w-full justify-between mt-8">
           <div className="w-full">
               <label className="text-xs font-bold">English Exam Status <span className='text-xs text-red-600 ml-1'>*</span></label>
               <div className="grid gap-4 grid-cols-1 md:flex mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="giv">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' checked={eng.exam_status === 'Already Gave the Exam' ? true : false} onChange={(e) => handleChange(e, i)} name="exam_status" value='Already Gave the Exam' className="check_input hidden" id="giv" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Already Gave The Exam</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="yet">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="exam_status" checked={eng.exam_status === 'Yet to give exam' ? true : false} value='Yet to give exam' className="check_input hidden" id="yet" onChange={(e) => handleChange(e, i)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Yet To Give The Exam</span></div>
                </div>
               </div>
              
           </div>
           
        </div>

     {
        eng.exam_status === 'Yet to give exam' ? (
            <div className="flex w-full justify-between mt-6">
            <div className="w-full relative flex items-center justify-start">
            <span class="text-xs font-bold justify-start mr-2"><input type="checkbox" onChange={(e) => handleCheckBOx(e, i)} class="custon_checkbox relative mr-2" name='need_help' value={true} /></span>
            Do you need help with English Exam Preparation?  
            </div>
             
            
         </div>
        ) : ''
     }
        {
            eng.exam_status === 'Already Gave the Exam' ? (
                <>
                <div className="flex w-full justify-between mt-6">
           <div className="w-full">
               <label className="text-xs font-bold">English Exam Completed <span className='text-xs text-red-600 ml-1'>*</span></label>
               <div className="grid gap-4 grid-cols-2 md:flex mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="ie">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' checked={eng.test_name === 'IELTS' ? true : false} name="test_name" value='IELTS' onChange={(e) => handleChange(e, i)} className="check_input hidden" id="ie" />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">IELTS</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="to">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' name="test_name" checked={eng.test_name === 'TOEFL' ? true : false} value='TOEFL' className="check_input hidden" id="to" onChange={(e) => handleChange(e, i)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">TOEFT</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="pt">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' checked={eng.test_name === 'PTE' ? true : false} name="test_name" value='PTE' className="check_input hidden" id="pt" onChange={(e) => handleChange(e, i)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">PTE</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="du">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' checked={eng.test_name === 'Duolingo' ? true : false} name="test_name" value='Duolingo' className="check_input hidden" id="du" onChange={(e) => handleChange(e, i)} />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Duolingo</span></div>
                </div>
               </div>
               
           </div>
           
           
        </div>

        {
            eng.test_name === 'IELTS' ? (
                <div className="flex flex-wrap w-full justify-between mt-4">
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Overall <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' max={9} step={.1} className='wizard_input'  name='over_all' value={eng.over_all} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Speaking <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={.1} max={9} className='wizard_input' name='speaking' value={eng.speaking} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Listening <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={.1} max={9} className='wizard_input' name='listening' value={eng.listening} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] xl:w-[19%]">
               <label className="text-xs font-bold">Reading <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={.1} max={9} className='wizard_input' name='reading' value={eng.reading} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-full md:w-[49%] xl:w-[19%]">
               <label className="text-xs font-bold">Writing <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={.1} max={9} className='wizard_input' name='writing' value={eng.writing} onChange={(e) => handleChange(e, i)} />
           </div>
           
        </div>
            ) : ''
        }

{
            eng.test_name === 'TOEFL' ? (
                <div className="flex flex-wrap w-full justify-between mt-4">
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Overall <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' max={120} step={1} className='wizard_input'  name='over_all' value={eng.over_all} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Speaking <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={1} max={30} className='wizard_input' name='speaking' value={eng.speaking} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Listening <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={1} max={30} className='wizard_input' name='listening' value={eng.listening} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] xl:w-[19%]">
               <label className="text-xs font-bold">Reading <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={1} max={30} className='wizard_input' name='reading' value={eng.reading} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-full md:w-[49%] xl:w-[19%]">
               <label className="text-xs font-bold">Writing <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={1} max={30} className='wizard_input' name='writing' value={eng.writing} onChange={(e) => handleChange(e, i)} />
           </div>
           
        </div>
            ) : ''
        }

{
            eng.test_name === 'PTE' ? (
                <div className="flex flex-wrap w-full justify-between mt-4">
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Overall <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' min={10} max={90} step={1} className='wizard_input'  name='over_all' value={eng.over_all} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Speaking <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={1} min={10} max={90} className='wizard_input' name='speaking' value={eng.speaking} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Listening <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={1} min={10} max={30} className='wizard_input' name='listening' value={eng.listening} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] xl:w-[19%]">
               <label className="text-xs font-bold">Reading <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={1} min={10} max={30} className='wizard_input' name='reading' value={eng.reading} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-full md:w-[49%] xl:w-[19%]">
               <label className="text-xs font-bold">Writing <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' step={1} min={10} max={30} className='wizard_input' name='writing' value={eng.writing} onChange={(e) => handleChange(e, i)} />
           </div>
           
        </div>
            ) : ''
        }

{
            eng.test_name === 'Duolingo' ? (
                <div className="flex flex-wrap w-full justify-between mt-4">
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Overall <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' min={10} max={160} step={1} className='wizard_input'  name='over_all' value={eng.over_all} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Conversation <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' min={10} step={1} max={30} className='wizard_input' name='speaking' value={eng.speaking} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] md:w-[32%] xl:w-[19%]">
               <label className="text-xs font-bold">Literacy <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' min={10} step={1} max={30} className='wizard_input' name='listening' value={eng.listening} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-[49%] xl:w-[19%]">
               <label className="text-xs font-bold">Comprehension <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' min={10} step={1} max={30} className='wizard_input' name='reading' value={eng.reading} onChange={(e) => handleChange(e, i)} />
           </div>
           <div className="w-full md:w-[49%] xl:w-[19%]">
               <label className="text-xs font-bold">Production <span className='text-xs text-red-600 ml-1'>*</span></label>
               <input type='number' min={10} step={1} max={30} className='wizard_input' name='writing' value={eng.writing} onChange={(e) => handleChange(e, i)} />
           </div>
           
        </div>
            ) : ''
        }
                </>
            ) : ''
        }



        <div className="mt-4 flex items-center justify-between">
        <button
           onClick={() => setCurrentStep(2)}
            className="text-sm font-bold text-[#ffffff] bg-black py-2 px-6 rounded-3xl inline-flex items-center">
                Back</button>
           <button
            onClick={() => handleUpdate(i)}
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center">
                {saving ? <span className='load'></span> : ''}
                Next</button>

            
        </div>
        </div>
                ))
            }
        </div>
    )
}

export default RequirementEnglish;