import { useNavigate } from "react-router-dom";

const LoginAlert = ({setDisplayAlert}) => {

    const navigate = useNavigate();

    if(!sessionStorage.getItem('is_login')){
        return (
            <div className="fixed hidden md:block p-4 shadow-md bottom-2 right-4 bg-[#ffffff] zind rounded-2xl">
                <button onClick={() => setDisplayAlert(false)}>X</button>
                    <div className="mb-4">
                        <h1 className="text-2xl text-center font-semibold">You are signed out</h1>
                    </div>
                    <div className="mb-6">
                        <p className="text-sm text-center font-semibold">Sign in to get best exprience</p>
                    </div>
                    <div>
                    <button 
                      onClick={() => navigate('/login')}
                      className="text-sm w-full text-[#ffffff] bg-primary py-3 px-6 rounded-3xl">Sign Up</button>
                    </div>

                    <div className="social-login mt-2 flex justify-center space-x-3 w-[250px] mx-auto">
        
      
        </div>

        <div className="flex justify-center mt-2">
         <span className="text-xs w-[250px] text-center text-[#7C878E] leading-5">
           By continuing, you agree to applyassist's Terms of Service and acknowledge you've read our Privacy Policy. Notice at collection.
         </span>
        </div>
                
            </div>
        )
    }
}

export default LoginAlert;