import { useState, useEffect } from "react";
import { savePersonalData, getApplicationById } from "../../Http";
import { toast } from 'react-toastify';
import { VerifyPersonalData } from "../../verify/personamData";
import { useParams } from "react-router-dom";


const RequirementPersonal = ({personalData, currentStep, setCurrentStep, setApplicationData}) => {

    const { id } = useParams()

    const [saving, setSaving] = useState(false)
    const [currentData, setCurrentData] = useState({
        first_name: null,
        last_name: null,
        dob: null,
        country_of_birth: null,
        gender: null,
        nationality: null,
        dual_citizenship: null,
        marital_status: null,
        address: null,
        state: null,
        city: null,
        postal_code: null
    })


    useEffect(() => {
        const fetchData = async () => {
            const {data} = await getApplicationById(id);
            console.log('personal', data)
            if(data.personal_info){
                setCurrentData(data.personal_info)
            }
            
        }
        fetchData()
    }, [])


    const handldeChnage = (e) => {
        setCurrentData({...currentData, [e.target.name]: e.target.value});
        console.log(currentData)
    }

    const handleCheckBox = (e) => {
        console.log(e)
        // if(e.target.checked){
        //     setCurrentData({
        //         ...currentData,
        //         [e.target.name]: e.target.value
        //     })
        //     console.log(currentData)
        // }
    }

    const handleUpdate = async () => {
        if(!VerifyPersonalData(currentData)){
            return (
                toast.error('Every field is required', {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                  })
            )
        }
        setSaving(true)

        const {data} = await savePersonalData({
            ...currentData,
            application_id: id
        });
        const application = await getApplicationById(id);
        setApplicationData(application.data)
        setCurrentStep(2)
        console.log(data)
    }

    return (
        <div className="w-full">
             <h1 className="text-xl font-semibold mr-6 my-6">Personal Detiles</h1>
           <div className="xl:w-[60%]">
           <div className="flex flex-col my-2 md:flex-row md: justify-between">
               <div className="w-full md:w-[49%]">
                  <label className="text-xs font-bold">First Name <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input className='wizard_input' name='first_name' onChange={(e) => handldeChnage(e)} value={currentData.first_name} placeholder="select a Test" />
               </div>
               <div className="w-full md:w-[49%]">
                  <label className="text-xs font-bold">Last Name <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input className='wizard_input' onChange={(e) => handldeChnage(e)}  name='last_name' value={currentData.last_name} placeholder="select a Test" />
               </div>
            </div>
            <div className="flex my-2 flex-col md:flex-row md: justify-between">
              <div className="w-full md:w-[49%]">
                  <label className="text-xs font-bold">DOB <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input type="date" className='wizard_input' value={currentData.dob} onChange={(e) => handldeChnage(e)} placeholder="select a Test" name='dob' />
               </div>
               <div className="w-full md:w-[49%]">
               <label className="text-xs font-bold">Gender <span className='text-xs text-red-600 ml-1'>*</span></label>
               <div className="grid grid-cols-2 gap-4 md:grid-cols-4 mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="mal">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                            <input type='radio' checked={currentData.gender === 'Male' ? true : false} onChange={(e) => handldeChnage(e)} name="gender" className="check_input hidden" id="mal" value='Male' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Male</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="fe">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input type='radio' checked={currentData.gender === 'Female' ? true : false} onChange={(e) => handldeChnage(e)} name="gender" className="check_input hidden" id="fe" value='Female' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Female</span></div>
                </div>
               </div>
               </div>
            </div>
            <div className="flex my-2 flex-col md:flex-row md: justify-between">
            <div className="w-full md:w-[49%]">
                  <label className="text-xs font-bold">Country of Birth <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <select className='wizard_input' onChange={(e) => handldeChnage(e)} name="country_of_birth" placeholder="select a Test">
                    <option>Select</option>
                    <option value='India' selected={currentData.country_of_birth === 'India' ? true : false}>India</option>
                    <option value='Nepal' selected={currentData.country_of_birth === 'Nepal' ? true : false}>Nepal</option>
                    <option value='Bangladesh' selected={currentData.country_of_birth === 'Bangladesh' ? true : false}>Bangladesh</option>
                    <option value='Others' selected={currentData.country_of_birth === 'Others' ? true : false}>Others</option>
                  </select>
               </div>
               <div className="w-full md:w-[49%]">
                  <label className="text-xs font-bold">Nationality <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <select className='wizard_input' onChange={(e) => handldeChnage(e)} name="nationality" placeholder="select a Test">
                    <option>Select</option>
                    <option value='India' selected={currentData.nationality === 'India' ? true : false}>India</option>
                    <option value='Nepal' selected={currentData.nationality === 'Nepal' ? true : false}>Nepal</option>
                    <option value='Bangladesh' selected={currentData.nationality === 'Bangladesh' ? true : false}>Bangladesh</option>
                    <option value='Others' selected={currentData.nationality === 'Others' ? true : false}>Others</option>
                  </select>
               </div>
            </div>
            <div className="flex my-2 flex-col md:flex-row md: justify-between">
            <div className="w-full md:w-[49%]">
                  <label className="text-xs font-bold">Dual Citizenship / Nationality <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <select name="dual_citizenship" onChange={(e) => handldeChnage(e)} className='wizard_input' placeholder="select a Test">
                    <option>Select</option>
                    <option value='Yes' selected={currentData.dual_citizenship === 'Yes' ? true : false}>Yes</option>
                    <option value='No' selected={currentData.dual_citizenship === 'No' ? true : false}>No</option>
                  </select>
               </div>
               <div className="w-full md:w-[49%]">
               <label className="text-xs font-bold">Marital Status <span className='text-xs text-red-600 ml-1'>*</span></label>
               <div className="grid grid-cols-2 gap-4 md:grid-cols-4 mb-4 mt-2">
                <div className="flex items-center mr-6">
                    <label htmlFor="mar">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input onChange={(e) => handldeChnage(e)} checked={currentData.marital_status === 'Married' ? true : false} type='radio' name="marital_status" className="check_input hidden" id="mar" value='Married' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Married</span></div>
                </div>
                <div className="flex items-center mr-6">
                    <label htmlFor="unm">
                        <div className="w-[20px] h-[20px] border-2 border-[#000] rounded-[50%] flex justify-center items-center">
                        <input checked={currentData.marital_status === 'Unmarried' ? true : false} onChange={(e) => handldeChnage(e)} type='radio' name="marital_status" className="check_input hidden" id="unm" value='Unmarried' />
                            <div className="w-[14px] h-[14px] border bg-primary rounded-[50%] opacity-0 check"></div>
                        </div>
                    </label>
                    <div><span className="ml-2">Unmarried</span></div>
                </div>
               </div>
               </div>
            </div>
             
            <div className="my-2">
              <label className="text-xs font-bold">Address <span className='text-xs text-red-600 ml-1'>*</span></label>
              <textarea value={currentData.address} placeholder="select a Test" onChange={(e) => handldeChnage(e)} name="address" className="profile_input" id="textarea"></textarea>
            </div>

            <div className="flex my-2 flex-col flex-wrap md:flex-row md: justify-between">
               <div className="w-full md:w-[49%] lg:w-[32%]">
                  <label className="text-xs font-bold">State <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input className='wizard_input' value={currentData.state} onChange={(e) => handldeChnage(e)} name='state' placeholder="select a Test" />
               </div>
               <div className="w-full md:w-[49%] lg:w-[32%]">
                  <label className="text-xs font-bold">City <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input className='wizard_input' value={currentData.city} onChange={(e) => handldeChnage(e)} placeholder="select a Test" name='city' />
               </div>
               <div className="w-full lg:w-[32%]">
                  <label className="text-xs font-bold">Postal Code <span className='text-xs text-red-600 ml-1'>*</span></label>
                  <input className='wizard_input' value={currentData.postal_code} onChange={(e) => handldeChnage(e)} placeholder="select a Test" name='postal_code' />
               </div>
            </div>
            <div className="mt-4">
           <button
           onClick={handleUpdate}
            className="text-sm font-bold text-[#ffffff] bg-primary py-2 px-6 rounded-3xl inline-flex items-center">
                {saving ? <span className='load'></span> : ''}
                Next</button>
        </div>
           </div>
        </div>
    )
}

export default RequirementPersonal;